import React, { useState } from 'react';
import { Button, Input, FormControl, Label, HelperMessage, useLoading } from '@yamada-ui/react';

function Signup() {
  const { screen } = useLoading();
  const [isSubmitting, setSubmitting] = useState(false);

  const handleSignup = async (event) => {
    event.preventDefault();
    setSubmitting(true);
    screen.start();

    try {
      await new Promise(resolve => setTimeout(resolve, 2000));
    } finally {
      setSubmitting(false);
      screen.finish();
    }
  };

  return (
    <div className="slideshow bg-gradient-to-b from-transparent to-black via-transparent opacity-75 fixed inset-0 z-0">
      <div className="max-w-md w-full mx-auto p-4 mt-20 bg-white bg-opacity-50 rounded-lg shadow-lg">
        <form onSubmit={handleSignup}>
          <FormControl label="">
            <Label></Label>
            <Input type='text' placeholder='' />
            <HelperMessage></HelperMessage>
          </FormControl>
          <FormControl label="">
            <Label></Label>
            <Input type='password' placeholder='' />
            <HelperMessage>6</HelperMessage>
          </FormControl>
          <FormControl label="">
            <Label></Label>
            <Input type='password' placeholder='' />
            <HelperMessage></HelperMessage>
          </FormControl>
          <Button type='submit' isLoading={isSubmitting} loadingText="..."></Button>
        </form>
      </div>
    </div>
  );
}

export default Signup;
